.b-post {
	color: #000;
	padding-bottom: 60px;
	h2 {
		margin-top: 32px;
		margin-bottom: 32px;
		font-size: 22px;
		font-style: normal;
		line-height: 30px;
	}
	&__title-wrap {
		padding-bottom: 56px;
	}
	&__title {
		padding-bottom: 40px;
		padding-top: 16px;
		font-size: 44px;
		font-weight: 500;
		line-height: normal;
	}

  &__date {
    color: #48484C;
    font-size: 13px;
    font-weight: 300;
    line-height: 16px;
  }


	&__img {
		max-width: 100%;
		height: auto;
	}
	&__subtitle {
		padding-top: 32px;
		padding-bottom: 32px;
		font-size: 22px;
		font-weight: 500;
	}
  &__main {
    padding-right: 48px;
    img {
      width: auto;
      height: auto;
      max-width: 100%;
      margin-bottom: 40px;
    }
    p {
      margin-bottom: 24px;
      font-weight: 300;
      line-height: 26px;
    }
    ul {
      padding-left: 12px;
      color: #000;
      font-size: 16px;
      font-weight: 300;
      line-height: 26px;
    }
    strong {
      font-weight: 700;
    }
    a {
      text-decoration: underline;
      color: #000;
      &:hover {
        color: var(--primary);
      }
    }
  }

  @include media-breakpoint-down(md) {
    &__main {
      padding-right: 0;
    }
  }

  @include media-breakpoint-down(sm) {
    &__title {
      font-size: 28px;
    }
    &__subtitle {
      font-size: 18px;
    }
  }
}


.b-post-sidebar {
  &__share {
    display: flex;
    align-items: center;
  }
  &__share-link {
    margin-left: 8px;
    &:hover {
      transform: scale(1.05);
      transition: .3s;
    }
  }

  &__title {
    display: flex;
    align-items: center;
    padding-bottom: 24px;
    border-bottom: 2px solid #000;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.2;

    + .b-post-sidebar__title {
      margin-top: 80px;
    }
  }
  &__item {
    display: block;
    padding-top: 24px;
    padding-bottom: 12px;
    color: #161617;
    &:not(:last-child) {
      border-bottom: 1px solid #000;
    }
  }
  &__text {
    padding-bottom: 12px;
    line-height: 22px;
  }
  &__number {
    color: #48484C;
    font-size: 13px;
    font-weight: 300;
    line-height: 1.37;
  }
  &__date {
    color: #48484C;
    font-size: 13px;
    font-weight: 300;
    line-height: 1.23;
  }
  &__date-lg {
    padding-bottom: 4px;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.33;
  }

  @include media-breakpoint-down(md) {
    &__title {
      display: block;
    }
    &__share {
      margin-top: 16px;
    }
    &__share-link {
      margin-left: 0;
      margin-right: 8px;
    }
    &__number-lg {
      font-size: 18px;
    }
    &__number {
      font-size: 14px;
    }
  }

  @include media-breakpoint-down(sm) {
    padding-top: 40px;
    border-top: 1px solid #000;
    &__title {
      font-size: 18px;
      + .b-post-sidebar__title {
        margin-top: 40px;
      }
    }
  }
}
