.b-login {
	padding-top: 48px;
	padding-bottom: 100px;
	&__form {
		display: flex;
		justify-content: center;
	}
	&__wrap {
		width: 480px;
	}
  &__note {
    text-align: center;
    color: #454545;
    font-size: 14px;
    line-height: 1.28;
  }
	&__btn {
		display: flex;
		width: 100%;
    height: 56px;
		justify-content: center;
		align-items: center;
		align-self: stretch;
    border-radius: 9px;

    .ic {
      margin-left: 12px;
    }
	}

  @include media-breakpoint-down(sm) {
    width: 100%;
  }
}

.b-login-nav {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;

  &__inner {
    display: inline-flex;
    justify-content: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.10);
  }
  &__item {
    text-align: center;
    margin-right: 36px;
    padding-top: 20px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__link {
    display: block;
    padding-bottom: 20px;
    color: #000;
    font-size: 16px;
    font-weight: 500;
    line-height: normal;
    .ic {
      margin-right: 12px;
      stroke: black;
      transition: all .3s;
    }
    &.active,
    &:hover {
      color: #E06605;
      border-bottom: 2px solid #E06605;
      .ic {
        stroke: #E06605;
      }
    }
    &.active {
      pointer-events: none;
    }
  }
}
