.main-nav {
  width: 100%;
  padding: 0;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 12px;
    padding-bottom: 12px;
    .btn {
      padding: 9px 16px 9px 12px;
      line-height: normal;
      border-radius: 9px;

      .ic {
        margin-right: 8px;
      }
    }
  }

  @include media-breakpoint-down(md) {
    &__inner {
      position: fixed;
      top: 108px;
      left: -100%;
      max-height: calc(100vh - 108px);
      display: flex !important;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      padding: 20px;
      background-color: #FDF7ED;
      transition: all .3s;
      z-index: 1000;
      overflow-y: auto;
      &.show {
        left: 0;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    &__inner {
      top: 78px;
      max-height: calc(100vh - 78px);
    }
  }

  @include media-breakpoint-up(lg) {
    &__inner {
      .header-soc {
        display: none;
      }
    }
  }

  @include media-breakpoint-up(md) {
    &__inner {
      .header-top__user {
        display: none;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    &__inner {
      .btn {
        width: 100%;
      }
    }
  }
}

.main-menu {
  display: flex;
  margin-bottom: 0;
  &__item {
    margin-right: 36px;
  }
  &__link {
    padding-top: 8px;
    padding-bottom: 8px;
    color: rgba(56, 51, 46, 0.97);
    font-size: 14px;
    line-height: normal;
    letter-spacing: 0.004px;
    &:hover {
      text-decoration: none;
    }
  }

  @include media-breakpoint-down(md) {
    &__item {
      margin-right: 0;
      margin-bottom: 10px;
    }
    .dropdown-menu {
      border: none;
      background-color: transparent;
    }
  }
}
