.b-office {
  padding-bottom: 44px;
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 56px;
  }

  .ui-title-page {
    margin-bottom: 0;
  }


}

.b-office-btn__block {
  display: flex;
}

.b-office-notif__btn {
  margin-right: 16px;
  border: 1px solid rgba(207, 139, 84, 0.20);
  color: #CF8B54;
  line-height: normal;
  &:hover {
    background-color: #CF8B54;
    color: #fff;
    .ic {
      stroke: #fff;
    }
  }
  .ic {
    transition: all .3s;
    stroke: #CF8B54;
  }
}
.b-office-view__btn {
  border: 1px solid #101010;
  opacity: 0.6;
  color: #000000;
  &:hover {
    color: #fff;
    background-color: #6C757D;
    border-color: #6C757D;
    opacity: 1;
  }
}

.b-office-block {
  margin-bottom: 16px;
  padding-bottom: 20px;
  padding-right: 20px;
  padding-left: 20px;
  border-radius: 13px;
  background: #F3F4F6;
  &__title {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    padding-top: 24px;
    padding-bottom: 20px;
    font-size: 18px;
    line-height: normal;
    border-bottom: 1px solid #E4E6E7;
    .ic {
      margin-right: 10px;
    }
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    font-size: 14px;
    font-weight: 300;
    line-height: 18px;
    color: #000000;
    &:hover {
      transition: .3s;
      color: #EE7B20;
    }
    .badge {
      margin-left: 10px;
    }
  }
}

.b-office-card {
  display: flex;
  margin-bottom: 16px;
  padding: 24px;
  border-radius: 13px;
  background: #F3F4F6;
  &__img {
    align-self: start;
    width: 80px;
    height: auto;
  }
  &__block {
    margin-left: 20px;
  }
  &__status {
    display: inline-flex;
    align-items: center;
    padding: 4px 6px;
    border-radius: 4px;
    color: #C7300F;
    background: rgba(238, 52, 32, 0.10);
    font-size: 11px;
    font-weight: 300;
    line-height: 16px;
    .ic {
      padding-right: 4px;
    }
  }
  &__title {
    font-size: 20px;
    font-weight: 500;
    line-height: 28px;
    margin-bottom: 8px;
    margin-top: 12px;
  }
  &__subtitle {
    padding-bottom: 16px;
    font-size: 14px;
    font-weight: 300;
    line-height: 18px;
    color: #000;
  }
  .btn {
    margin-right: 10px;
    padding: 6px 16px;
  }
  .btn-outline-secondary {
    color: #393939;
    &:hover {
      color: #FFF;
    }
  }
}

@include media-breakpoint-down(lg) {
  .b-office__row {
    display: block;
    padding-bottom: 30px;
  }

  .b-office-block {
    padding-right: 12px;
    padding-left: 12px;
    &__item {
      font-size: 13px;
    }
    .badge {
      margin-left: 6px;
      width: 20px;
      height: 20px;
    }
  }
  .b-office {
    .ui-title-page {
      margin-bottom: 20px;
    }
  }
}


@include media-breakpoint-down(md) {
  .b-office-btn__block {
    display: block;
    margin-bottom: 10px;
  }
  .b-office-notif__btn {
    width: 100%;
    margin-right: 0;
    margin-bottom: 16px;
  }
  .b-office-view__btn {
    width: 100%;
  }
}

@include media-breakpoint-down(sm) {
  .b-office-card {
    display: block;
    text-align: center;
    .btn {
      width: 100%;
      margin-bottom: 20px;

    }
    &__img {
      margin: auto;
      text-align: center;
      padding-bottom: 12px;
    }
    &__block {
      margin: 0;
    }
  }
}
