.section-news {
	&__btns {
    padding-right: 124px;
		padding-bottom: 28px;
	}
	&__btn {
		display: inline-flex;
		height: 32px;
		padding: 8px 16px;
		margin-bottom: 8px;
		margin-right: 1px;
		justify-content: center;
		align-items: center;
		gap: 10px;
		border-radius: 20px;
		border: 1px solid rgba(238, 123, 32, 0.30);
		background: transparent;
		color: #A1714B;
		font-size: 15px;
		font-weight: 300;
		&:hover {
			background: #EE7B20;
			color: #FFF;
			transition: .3s;
		}
		&.btn-active {
  		background: #EE7B20;
  		color: #FFF;
  	}
	}

  @include media-breakpoint-down(sm) {
    &__btns {
    	display: flex;
    	flex-direction: column;
      padding-right: 0;
    }
  }
}

.b-news-group {
  padding-top: 120px;

  @include media-breakpoint-down(sm) {
    padding-top: 60px;
  }
}

.b-news-slider {
  margin-right: -30px;
  &__item {
    padding-right: 30px;
  }

  .b-news {
    padding-bottom: 0;
  }

  @include media-breakpoint-down(sm) {
    margin-right: 0;
    margin-top: 78px;
    &__item {
      padding-right: 0;
    }
  }
}


.b-news {
  padding-bottom: 60px;
	&__info {
		display: flex;
		padding: 16px 0;
		opacity: 0.5;
		color: #2C2620;
		font-family: e-Ukraine;
		font-size: 13px;
		font-weight: 300;
		line-height: 12px;
	}
	
	&__img {
		width: 100%;
		object-fit: cover;
		aspect-ratio: 350/186;
	}
	&__views {
		position: relative;
		padding-left: 32px;
		padding-right: 16px;
		.ic {
			position: absolute;
			top: 0;
			left: 15px;
		}
	}
	&__title {
		height: 86px;
		margin-bottom: 16px;
		font-size: 20px;
		color: #161617;
		font-weight: 500;
		line-height: 1.4;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
	}

	&__button {
		height: 44px;
    padding-right: 16px;
    .ic {
      margin-left: 6px;
    }
	}

  &__tags {
    display: flex;
    flex-wrap: wrap;
    margin-top: 16px;
    margin-bottom: 10px;
  }
  &__tag {
    color: #8A817E;
    font-size: 13px;
    font-weight: 300;
    line-height: 1.69;
  }
  &__text {
    margin-bottom: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    color: var(--, #161617);
    font-size: 15px;
    font-weight: 300;
    line-height: 1.46;
  }

  &_mod-a & {
    &__title {
      height: auto;
      display: block;
    }
  }

  @include media-breakpoint-down(sm) {
    &__title {
      height: auto;
      font-size: 16px;
    }
  }
}



.b-news-2 {
  display: block;
  padding-top: 16px;
  margin-bottom: 44px;
  color: #161617;
  font-weight: 300;
  line-height: 26px;
  &:hover {
    opacity: .8;
  }

  &__img {
    width: 100%;
    aspect-ratio: 350/186;
    object-fit: cover;
  }

  &__info {
    display: flex;
    align-items: flex-start;
    padding-top: 16px;
  }
  &__date {
    display: flex;
    flex-direction: column;
    margin-right: 12px;
    padding-right: 12px;
    color: #161617;
    border-right: 1px solid rgba(0, 0, 0, 0.10);
  }
  &__number {
    font-size: 36px;
    font-weight: 700;
    line-height: .86;
  }
  &__month {
    margin-top: 2px;
    margin-bottom: 2px;
    font-weight: 500;
    line-height: normal;
  }
  &__year {
    font-size: 14px;
    font-weight: 300;
    line-height: 1.28;
  }
  &__title {
    font-size: 20px;
    font-weight: 500;
    line-height: 1.4;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  @include media-breakpoint-down(sm) {
    padding-top: 0;
    &__title {
      font-size: 16px;
    }
    &__number {
      font-size: 26px;
    }
    &__month {
      font-size: 15px;
    }
    &__year {
      font-size: 13px;
    }
  }
}
