.b-review-form {
  max-width: 750px;
}

.b-review {
  padding-top: 16px;
  padding-bottom: 60px;
  &__header {
    display: flex;
    justify-content: space-between;
    padding-bottom: 24px;
  }
  &__header-select {
    width: 258px;
    .ui-select {
      padding: 12px 16px;
      color: #000;
      font-size: 15px;
      font-weight: 400;
      line-height: 20px;
      border-radius: 8px;
      border: 1px solid #EFEAE6;
      background: #FFF;
      &.dropdown-toggle:after {
        top: 17px;
        border-top: 2px solid #E06605;
        border-left: 2px solid #E06605;
      }
    }
  }

  &__submit {
    width: 360px;
    padding-top: 18px;
    padding-bottom: 18px;
    line-height: normal;
    letter-spacing: 0.004px;
  }

  &__item {
    margin-bottom: 24px;
    padding: 24px;
    border: 1px solid rgba(0, 0, 0, 0.18);
    border-radius: 8px;
    .ui-star-radio-group {
      margin-bottom: 8px;
    }
    .ui-star-radio__label {
      width: 18px;
      height: 18px;
      background-size: cover;
      background-repeat: no-repeat;
      &:not(.active) {
        background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="38" height="37" viewBox="0 0 38 37" fill="none"><path d="M18.1156 1.67634C18.4908 0.965195 19.5092 0.965192 19.8844 1.67634L24.8835 11.1513C25.0282 11.4256 25.2919 11.6172 25.5975 11.6701L36.1535 13.4965C36.9458 13.6336 37.2605 14.6022 36.7001 15.1788L29.2337 22.8612C29.0176 23.0835 28.9168 23.3936 28.961 23.7005L30.4859 34.3043C30.6004 35.1002 29.7764 35.6988 29.0548 35.344L19.4412 30.617C19.163 30.4801 18.837 30.4801 18.5588 30.617L8.94516 35.344C8.22361 35.6988 7.39964 35.1002 7.51409 34.3043L9.03902 23.7005C9.08316 23.3936 8.98242 23.0835 8.76631 22.8612L1.29986 15.1788C0.73946 14.6022 1.05419 13.6336 1.84647 13.4965L12.4025 11.6701C12.7081 11.6172 12.9718 11.4256 13.1165 11.1513L18.1156 1.67634Z" stroke="%23F3BC30" stroke-width="2"/></svg>');
      }
    }
  }
  &__block {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-right: 24px;
  }
  &__author {
    margin-right: 16px;
    margin-bottom: 8px;
  }
  &__date {
    margin-bottom: 8px;
    opacity: .5;
    font-weight: 400;
    font-size: 13px;
  }
  &__info {
    margin-top: 16px;
  }
  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 14px;
  }
}

.b-review-group {
  margin-bottom: 40px;
}

@include media-breakpoint-down(sm) {
  .form-wrap {
    display: block;
  }
  .b-review__header {
    display: block;
  }
  .b-review__submit {
    width: 100%;
  }
}
