.footer {
  margin-top: 56px;
	padding-top: 40px;
	padding-bottom: 24px;
	background: #1E1C1A;
	color: #FFF;
	a {
		color: #FFF;
		&:hover {
			opacity: 0.8;
		}
	}
}
.footer-wrap {
	flex-direction: column;
}
.footer-top {
  display: flex;
  justify-content: space-between;

  @include media-breakpoint-down(sm) {
    display: block;
    text-align: center;
  }
}

.footer-list {
  margin-bottom: 30px;
  padding-left: 0;
	list-style: none;
	a {
    display: inline-block;
		padding-bottom: 10px;
    opacity: 0.7;
    &:hover {
      opacity: 1;
    }
	}

  @include media-breakpoint-down(sm) {
    margin-bottom: 4px;
    padding-bottom: 20px;
  }
}

.footer-bottom {
	display: flex;
	justify-content: space-between;
	padding-top: 16px;
	border-top: 1px solid #373737;
	&__wrap {
		display: flex;
	}
	&__ic {
		display: inline;
		padding-right: 8px;
	}
  &__logo {
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    font-family: 'e-Ukraine Head';
    font-weight: 500;
    line-height: normal;
    .ic {
      width: 120px;
      margin-right: 14px;
    }
  }
  &__group {
    display: inline-flex;
    align-items: center;
  }
  &__copyright {
    font-size: 12px;
    font-weight: 300;
    line-height: normal;
    opacity: 0.4;
  }

  @include media-breakpoint-down(sm) {
    display: block;
    text-align: center;

    &__logo {
      display: block;
      margin-bottom: 20px;
    }
  }
}
