.b-brand {
	padding-bottom: 60px;
	&__row {
		display: flex;
		justify-content: space-between;
		.btn {
      display: flex;
      align-items: center;
			height: 52px;
			font-size: 14px;
			font-weight: 500;
			&:hover {
				.ic {
					stroke: #FFF;
				}
			}
			.ic {
        margin-right: 8px;
        transition: all .3s;
				stroke: #E06605;
			}
		}
	}
	&__form {
    margin-top: 16px;
		padding: 40px;
		border-radius: 13px;
		background: #FDF6EE;
	}
	.form-group {
		margin-bottom: 44px;
		
	}
	.form-wrap {
		display: flex;
		justify-content: space-between;
		.form-group {
			width: 50%;
		}
	}
	&__upload {
		display: flex;
		width: 100%;
		height: 60px;
		padding: 13px 24px;
		justify-content: center;
		align-items: center;
		align-self: stretch;
		background: transparent;
		color: #000;
		&:hover {
			background: #FFF;
			color: #000;
		}

    .ic {
      margin-right: 8px;
    }

    input {
      height: 0;
      width: 0;
      padding: 0;
      opacity: 0;
    }
		
	}
	&__group {
		padding-right: 44px;
	}
	&__btn-wrap {
		display: flex;
		justify-content: center;
		.btn {
			width: 220px;
			padding: 18px;
			.ic {
				margin-right: 6px;
			}
		}
	}
}



@include media-breakpoint-down(sm) {
  .b-brand {
	 padding-bottom: 60px;
		&__row {
			display: block;
			padding-bottom: 40px;
			text-align: center;

      .btn {
        width: 100%;
        justify-content: center;
      }
		}
		&__form {
			padding: 16px;
		}
		&__group {
			padding-right: 0;
		}
    &__btn-wrap {
      .btn {
        width: 100%;
      }
    }
		.form-wrap {
			display: block;
			.form-group {
				width: 100%;
			}
		}
	}
}
