.b-main {
  position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 20px 0;
	min-height: calc(100vh - 205px);
	background-size: cover;
	
	&__title {
    max-width: 850px;
    margin-left: auto;
    margin-right: auto;
		padding-bottom: 50px;
		text-align: center;
		color: var(--White, #FFF);
		font-size: 56px;
		font-weight: 500;
		line-height: 1.18;
	}
  &__bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  @include media-breakpoint-down(md) {
    min-height: calc(100vh - 58px);
  }


  @include media-breakpoint-down(sm) {
    &__title {
      font-size: 32px;
    }
  }
}
