.l-theme {
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.l-wrap {
  flex: 1 0 auto;
}

a {
  text-decoration: none;
  transition: all 0.3s;
}

p {
  line-height: 1.2;
  margin-bottom: 18px;
}

/* ======= GUI ======= */
.btn {
  padding: 10px 20px;
  color: #FFF;
  font-size: 14px;
  font-weight: 400;
  line-height: 152%;
  transition: all 0.3s;
  border-radius: 40px;
}

.btn-default {
  border-radius: 9px;
}

.btn-primary {
  stroke: #fff;
}
.btn-primary .ic {
  stroke: inherit;
}
.btn-outline-primary {
  color: #E06605;
}

/* Images */
.img-scale {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.ic-rot_90 {
  transform: rotate(90deg);
}

.ic-rot_-90 {
  transform: rotate(-90deg);
}

.ic-rot_180 {
  transform: rotate(180deg);
}

/* Lists */
.ui-list li {
  margin-bottom: 12px;
}
.ui-list li:before {
  content: "- ";
}

.ui-list-num {
  counter-reset: list;
  list-style-type: none;
  margin-bottom: 25px;
  line-height: 1.2;
  padding-left: 25px;
}
.ui-list-num li {
  position: relative;
}
.ui-list-num li:not(:last-child) {
  margin-bottom: 12px;
}
.ui-list-num li:before {
  position: absolute;
  top: 0;
  left: -20px;
  font-weight: 700;
  content: counter(list) ". ";
  counter-increment: list;
}

/* Forms */
.ui-btn-password {
  position: absolute;
  top: 16px;
  right: 16px;
  border: none;
  background-color: transparent;
}

.form-input-password {
  position: relative;
}

.form-group {
  margin-bottom: 32px;
}

.form-label {
  margin-bottom: 12px;
  color: #000;
}

.ui-date-picker-wrap {
  position: relative;
  display: flex;
  align-items: center;
  min-width: 258px;
}
.ui-date-picker-wrap:after {
  position: absolute;
  right: 16px;
  top: 12px;
  width: 16px;
  height: 16px;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none"><path d="M3 6L8 11L13 6" stroke="%23E16C10" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></svg>');
