.section-announc {
	background: #FCF4ED;


  @include media-breakpoint-down(md) {
    &__item {
      &:last-child {
        display: none;
      }
    }
  }
}

.b-announc {
  display: block;
  margin-top: 17px;
	color: #2C2620;

	&__date {
		padding-bottom: 2px;
		font-size: 32px;
		font-weight: 500;
		border-bottom: 1px solid #2C2620;
	}
	&__text {
		padding: 20px 0;
		font-size: 18px;
		line-height: 28px;
	}
}
