.b-map {
  position: relative;
  text-align: center;

  &__inner {
    height: 668px;
  }

  .container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 410;
  }

  &__form {
    padding-top: 16px;
    border: none;
  }
  &__form-block {
    display: flex;
    width: 100%;
    flex-grow: 1;
  }

  &__button {
    width: 100%;
    margin-top: 24px;
    padding: 14px 16px 14px 20px;
    border-radius: 40px;
    &:hover {
      box-shadow: none;
    }
  }
  &__search {
    display: flex;
    position: relative;
    width: 74px;
    height: 54px;
    padding: 0;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border-radius: 0px 10px 10px 0px;
    background: #EE7B20;
    .ic {
      width: 30px;
      height: 30px;
    }
    &:hover {
      background-color: #d76810;
      border-color: #cb6310;
    }
  }
  &__input {
    flex-grow: 1;
    padding-left: 12px;
    font-size: 12px;
    font-weight: 300;
    color: #797979;
    border-radius: 8px 0px 0px 8px;
    border: 1px solid var(--Block-inner, #DEDEDE);
    border-right: none;
  }
  &__select {
    width: 100%;
    height: 54px;
    padding-left: 12px;
    border-radius: 8px;
    margin-top: 24px;
    border: 1px solid var(--Block-inner, #DEDEDE);
    color: var(--unnamed, #181C26);
    font-size: 13px;
    font-weight: 300;
    line-height: normal;
  }

  &__wrap {
    position: absolute;
    left: 15px;
    top: 0;
    width: 444px;
    z-index: 100;
    padding: 32px;
    background: #FFF;
    border-radius: 0px 0px 13px 13px;
  }
  &__nav {
    margin-top: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.10);
    .nav-link {
      padding: 8px 16px;
      color: #000;
      font-size: 16px;
      line-height: 24px;
      font-weight: normal;
      opacity: 0.6;
      border-bottom: 2px solid transparent;
      &.active,
      &:hover {
        opacity: 1;
        border-bottom-color: #000;
      }
    }
  }


  &_mod-a & {
    &__wrap {
      width: 540px;
      padding: 48px 24px 32px 24px;
    }
  }

  @include media-breakpoint-down(lg) {
    &_mod-a & {
      &__wrap {
        width: 360px;
      }
    }
    &__wrap {
      width: 360px;
    }
    &__form {
      padding-top: 0;
    }
  }

  @include media-breakpoint-down(sm) {
    .container {
      position: static;
    }
    &__wrap {
      position: static;
    }
    &_mod-a & {
      &__wrap {
        width: 100%;
        padding: 24px 0;
      }
    }
    &__wrap {
      padding: 24px 0;
      width: 100%;
      overflow: hidden;
    }
    &__inner {
      height: 400px;
    }
  }
}

.b-map-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.b-map-slider {
  &__item {
    text-align: center;
  }
  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 64px;
    height: 64px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 5px;
    background: #F1F3F6;
  }
  &__name {
    margin-top: 12px;
    color: #000;
    text-align: center;
    font-size: 14px;
    font-weight: 300;
    line-height: normal;
  }
}

.b-map-slider-2 {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid #F0F0F0;
  &__item {
    text-align: center;
  }
  &__name {
    margin-top: 12px;
    color: #000;
    font-size: 14px;
    font-weight: 300;
    line-height: normal;
  }
  &__img {
    width: 100%;
    aspect-ratio: 204/123;
  }
}
