.b-links {
  padding-bottom: 74px;
	&__row {
		display: flex;
		flex-wrap: wrap;
    margin-right: -20px;
		padding-top: 60px;
	}
	&__item {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
    flex: 0 0 auto;
    width: calc(50% - 20px);
		padding: 16px;
		margin-right: 20px;
		margin-bottom: 20px;
		border-radius: 10px;
		border: 1px solid #DDD;
		&:hover {
			border: 1px solid #EE7B20;
			transition: .3s;
			.b-links__item-text {
				transition: .3s;
				color: #EE7B20;
			}
		}
	}
	&__item-text {
    width: calc(100% - 64px);
    flex: 0 0 auto;
		padding-left: 13px;
		color: #2C2620;
		font-size: 16px;
		line-height: 24px;
	}
	&__img {
		display: inline-block;
    width: 48px;
    height: 48px;
    flex: 0 0 auto;
	}


  @include media-breakpoint-down(sm) {
    &__row {
      margin-right: 0;
    }
    &__item {
      width: 100%;
    }
  }
}

.b-links-2 {
  position: relative;
  margin: 90px 0;
  filter: drop-shadow(2px 4px 6px #444);
  // border: 1px solid #F0F3F4;
  background: url(../img/content/b-links/Q-lieb-in1.jpg);
  background-size: cover;
  border-radius: 10px;

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .ui-title {
    margin-bottom: 40px;
    font-size: 36px;
    text-shadow: -1px -1px #bdbdbd;
    filter: drop-shadow(2px 2px 1px #fff);
  }
  .btn-outline-primary {
    background-color: #FFF;
    &:hover {
      background-color: #EE7B20;
    }
  }

  @include media-breakpoint-down(md) {
    .ui-section {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}
