.b-modal-success {
  &__btn {
    display: flex;
    align-items: center;
    margin: 0 12px;
  }

  @include media-breakpoint-up(lg) {
    &__btn {
      width: 266px;
    }
  }

  @include media-breakpoint-down(md) {
    &__btn {
      width: 100%;
      margin-left: 0;
      margin-right: 0;
      justify-content: center;
      + .b-modal-success__btn {
        margin-top: 8px;
      }
    }
  }
}

.modal-content {
	padding: 48px;
	text-align: center;
	border-radius: 24px;
	border: 1px solid #D1D1D1;
	background: #FFF;
}
.modal-header {
  justify-content: center;
  border: none;
  padding: 0 0 28px;
}
.modal-title {
  display: flex;
	color: #E06605;
	font-size: 24px;
	font-weight: 500;
	line-height: 28px;
  .ic {
    margin-right: 16px;
  }
}

.modal-body {
  padding: 0 0 12px;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}

.modal-footer {
  padding: 28px 0 0;
  border-top: none;
}

.modal-backdrop {
  &.show {
   	opacity: .15; 
  }
}

@include media-breakpoint-up(lg) {
  .modal-lg {
    max-width: 678px;
  }
}

@include media-breakpoint-down(md) {
	.modal-content {
		padding: 24px;
	}
}
