html,
body {
  height: 100%;
}
body {
  font-family: 'e-Ukraine';
  color: #2C2620;

  &.st-hidden {
    overflow: hidden;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 108px;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 999;
      background-color: #000;
      opacity: 0.7;
    }
  }

  @include media-breakpoint-down(sm) {
    &.st-hidden {
      overflow: hidden;
      position: relative;
      &:after {
        top: 78px;
      }
    }
  }
}

.l-theme {
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.l-wrap {
  flex: 1 0 auto;
}

a {
  text-decoration: none;
  transition: all .3s;
}

p {
  line-height: 1.2;
  margin-bottom: 18px;
}


/* ======= GUI ======= */


.btn {
  padding: 10px 20px;
  color: #FFF;
  font-size: 14px;
  font-weight: 400;
  line-height: 152%;
  transition: all .3s;
  border-radius: 40px;
}

.btn-default {
  border-radius: 9px;
}

.btn-primary {
  stroke: #fff;
  .ic {
    stroke: inherit;
  }
  &:hover {
    // background: #D55B0F;
  }
}
.btn-outline-primary {
  color: #E06605;

}

/* Images */
.img-scale {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.ic-rot_90 {
  transform: rotate(90deg);
}
.ic-rot_-90 {
  transform: rotate(-90deg);
}
.ic-rot_180 {
  transform: rotate(180deg);
}

/* Lists */
.ui-list {
  li {
    margin-bottom: 12px;
    &:before {
      content: '- ';
    }
  }
}


.ui-list-num {
  counter-reset: list;
  list-style-type: none;
  margin-bottom: 25px;
  line-height: 1.2;
  padding-left: 25px;
  li {
    position: relative;
    &:not(:last-child) {
      margin-bottom: 12px;
    }
    &:before {
      position: absolute;
      top: 0;
      left: -20px;

      font-weight: 700;

      content: counter(list) '. ';
      counter-increment: list;
    }
  }
}


/* Forms */
.ui-form {
}

.ui-btn-password {
  position: absolute;
  top: 16px;
  right: 16px;
  border: none;
  background-color: transparent;
}

.form-input-password {
  position: relative;
}
.form-group {
  margin-bottom: 32px;
}
.form-label {
  margin-bottom: 12px;
  color: #000;
}
.ui-date-picker-wrap {
  position: relative;
  display: flex;
  align-items: center;
  min-width: 258px;
  &:after {
    // content: '';
    position: absolute;
    right: 16px;
    top: 12px;
    width: 16px;
    height: 16px;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none"><path d="M3 6L8 11L13 6" stroke="%23E16C10" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></svg>');
  }
  .remove-btn {
    border: none;
    background-color: transparent;
  }
  .search-btn {
    border: none;
    background-color: transparent;
  }
}

.ui-date-picker {
  border: none;
  background-color: transparent;
  &__ic {
    display: flex;
    margin-right: 8px;
  }
  &:focus {
    outline: none;
  }

  @include media-breakpoint-down(md) {
    font-size: 14px;
    flex-grow: 1;
  }
}

.input-group input:valid + .input-group-addon .remove-btn {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}
.input-group input:placeholder-shown + .input-group-addon .remove-btn {
  opacity: 0;
  visibility: hidden;
}

.input-group .remove-btn:hover svg path {
    stroke: red;
}

select:focus {
  outline-style: none;
}


.ui-label {
  color: #0d0d0d;
  letter-spacing: 0.3px;
  margin-bottom: 13px;
}

.ui-select {
  padding: 12px 16px;
  color: #000;
  font-size: 15px;
  font-weight: 400;
  line-height: 20px;
  border-radius: 8px;
  border: 1px solid #EFEAE6;
  background: #FFF;
  &.dropdown-toggle:after {
    top: 17px;
    right: 15px;
    border-top: 2px solid #E06605;
    border-left: 2px solid #E06605;
  }
}

.dropup .ui-select-default.dropdown-toggle::after {
  &:after {
    top: 12px;
  }
}

/* Select validation*/
.was-validated .js-select {
  &:invalid + .ui-select {
    border-color: $form-feedback-invalid-color;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23dc3545' viewBox='-2 -2 7 7'%3e%3cpath stroke='%23dc3545' d='M0 0l3 3m0-3L0 3'/%3e%3ccircle r='.5'/%3e%3ccircle cx='3' r='.5'/%3e%3ccircle cy='3' r='.5'/%3e%3ccircle cx='3' cy='3' r='.5'/%3e%3c/svg%3E");
    background-repeat: no-repeat;
    background-size: calc(0.5em + 0.375rem) calc(0.5em + 0.375rem);
    padding-right: calc(1em + 0.75rem);
    background-position: top calc(0.25em + 0.1875rem) right calc(0.25em + 0.1875rem);
  }
  &:valid + .ui-select {
    border-color:$form-feedback-valid-color;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%2328a745' d='M2.3 6.73L.6 4.53c-.4-1.04.46-1.4 1.1-.8l1.1 1.4 3.4-3.8c.6-.63 1.6-.27 1.2.7l-4 4.6c-.43.5-.8.4-1.1.1z'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-size: calc(0.5em + 0.375rem) calc(0.5em + 0.375rem);
    padding-right: calc(1em + 0.75rem);
    background-position: top calc(0.25em + 0.1875rem) right calc(0.25em + 0.1875rem);
  }
}

a:hover {
 text-decoration: none;
}
.ui-title {
  color: #2C2620;
  font-size: 48px;
  font-weight: 400;
  line-height: normal;

  @include media-breakpoint-down(md) {
    font-size: 32px;
  }
}

.ui-title-page {
  margin-bottom: 40px;
  color: #000;
  font-size: 44px;
  font-weight: 500;
  line-height: normal;

  @include media-breakpoint-down(md) {
    font-size: 32px;
  }
}

/* Sections */

.section-default {
  position: relative;
  margin-top: 150px;

  @include media-breakpoint-down(xl) {
    margin-top: 80px;
  }
}
.ui-section {
  padding-top: 96px;
  padding-bottom: 96px;

  @include media-breakpoint-down(md) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
.ui-title__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 40px;
  .btn {
    min-width: 164px;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  @include media-breakpoint-down(sm) {
    flex-direction: column; 
    .ui-title {
      padding-bottom: 20px;
    }
  }
}

.ui-btn-up {
  position: fixed;
  right: 32px;
  bottom: 23px;
  display: none;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border-radius: 40px;
  border: 2px solid #FFF;
  background: #2C2620;
  box-shadow: 0px 1px 6px 0px rgba(0, 0, 0, 0.35);
  fill: #fff;
  cursor: pointer;
  transition: all .3s;
  z-index: 100;

  &:hover {
    background-color: var(--primary);
  }

  &.active {
    display: flex;
  }
  @include media-breakpoint-down(sm) {
    width: 0;
    height: 0;
    padding: 0;
    border: none;
    // width: 36px;
    // height: 36px;
    // right: 16px;
    // padding: 10px;
  }
}



