/* User content */
.user-content {
}

/* Other */

.fill-primary {
  fill: $primary;
}

.stroke-primary {
  stroke: $primary;
}
.dropdown-menu {
  font-size: 14px;
}
.dropdown-item {
  padding-left: 16px;
  padding-right: 16px;
  &:hover {
    background-color: var(--primary);
    color: #FFF;
  }
}
.dropdown-toggle {
  padding-right: 16px;
  &::after {
    position: absolute;
    right: 0;
    top: 8px;
    width: 8px;
    height: 8px;
    border-right: none;
    border-top: 2px solid #38332E;
    border-left: 2px solid #38332E;
    border-radius: 1px;
    transform: rotate(225deg);
    transition: all .3s;
  }
}




.nav-tabs {
  margin-bottom: 60px;
  margin-right: -40px;
   border: none;

  .nav-item {
    margin-right: 40px;
    .nav-link {
      padding: 0 0 16px;
      border-width: 0 0 2px;
      border-style: solid;
      border-color: transparent;
      color: #1D293B;
      text-align: center;
      font-family: Wadik;
      font-size: 18px;
      font-weight: 700;
      text-transform: uppercase;
      font-variant-numeric: lining-nums proportional-nums;
      &:hover {
          border-bottom-color: #EE6030;
          color: #FF7E35;
          transition: .3s;
      }
      &.active {
        border: none;
        border-bottom: 2px solid #EE6030;
        color: #FF7E35;
      }
    }
  }

  @include media-breakpoint-down(xl) {
    .nav-item {
      .nav-link {
        font-size: 14px;
      }
    }
  }

  @include media-breakpoint-down(lg) {
    margin-bottom: 40px;
    margin-right: 0;
    .nav-item {
      flex: 0 0 auto;
      width: 50%;
      margin-right: 0;
      padding: 0 10px;
      text-align: center;
      .nav-link {
        margin-bottom: 16px;
        padding-bottom: 6px;
        border-bottom-color: #1D293B;
      }
    }
  }

  @include media-breakpoint-down(md) {
    .nav-item {
      width: 100%;
    }
  }
}

.table-striped {
  tr {
    border: none;
    td {
      vertical-align: middle;
      border-bottom: none;
      padding: 16px;
    }
  }
  > tbody > tr:nth-of-type(odd) > * {
    background: #F6F6F6;
  }
}
.breadcrumb {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 16px;
  padding-top: 40px;
  font-size: 14px;
  color: #666;
  background: transparent;
  a {
    color: #1D293B;
    &:hover {
      color: var(--bs-primary);
    }
  }

  @include media-breakpoint-down(lg) {
    margin-bottom: 20px;
  }

  @include media-breakpoint-down(sm) {
    margin-bottom: 12px;
  }
}

.breadcrumb-item {
  display: inline-flex;
  align-items: center;
  + .breadcrumb-item {
    padding-left: 10px;
    &:before {
      content: '|';
    }
  }
}


.ui-pagination {
  display: flex;
  flex-grow: 1;
  flex: 0 0 auto;
  padding-top: 24px;
  padding-bottom: 60px;
  border-top: 1px solid #000;
  justify-content: space-between;
  text-align: center;
  .page-link {
    background: transparent;
    color: #000;
    border: none;
  }
  a.page-link {
    &:hover {
      color: var(--primary);
      border-color: var(--primary);
    }
  }
}

a.ui-pagination-btn {
  &:hover {
    color: var(--primary);
  }
}

span.ui-pagination-btn {
  opacity: 0.3;
  cursor: default;
}

.ui-pagination-btn {
  display: flex;
  align-items: center;
  color: #000;
  font-size: 13px;
  font-weight: 500;
  line-height: 18px;

  &_next {
    &:after {
      display: inline-block;
      background-image: url(../img/minified-svg/arr-right.svg);
      content:'';
      width: 24px;
      height: 24px;
      margin-left: 10px;
    }
  }
  &_prev {
    &:before {
      content:'';
      display: inline-block;
      width: 24px;
      height: 24px;
      background-image: url(../img/minified-svg/arr-left.svg);
      margin-right: 10px;
    }
  }


  @include media-breakpoint-down(sm) {
    font-size: 0;
  }
}

.ui-star-radio-group {
  display: inline-flex;
  align-items: center;
}

.ui-star-radio {
  &__input {
    width: 0;
    height: 0;
    padding: 0;
    overflow: hidden;

    &:checked {
      ~ .ui-star-radio__input ~ .ui-star-radio__label {
        background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="38" height="37" viewBox="0 0 38 37" fill="none"><path d="M18.1156 1.67634C18.4908 0.965195 19.5092 0.965192 19.8844 1.67634L24.8835 11.1513C25.0282 11.4256 25.2919 11.6172 25.5975 11.6701L36.1535 13.4965C36.9458 13.6336 37.2605 14.6022 36.7001 15.1788L29.2337 22.8612C29.0176 23.0835 28.9168 23.3936 28.961 23.7005L30.4859 34.3043C30.6004 35.1002 29.7764 35.6988 29.0548 35.344L19.4412 30.617C19.163 30.4801 18.837 30.4801 18.5588 30.617L8.94516 35.344C8.22361 35.6988 7.39964 35.1002 7.51409 34.3043L9.03902 23.7005C9.08316 23.3936 8.98242 23.0835 8.76631 22.8612L1.29986 15.1788C0.73946 14.6022 1.05419 13.6336 1.84647 13.4965L12.4025 11.6701C12.7081 11.6172 12.9718 11.4256 13.1165 11.1513L18.1156 1.67634Z" stroke="%23F3BC30" stroke-width="2"/></svg>');
      }
    }
  }
  &__label {
    width: 38px;
    height: 37px;
    margin-right: 4px;
    cursor: pointer;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="38" height="37" viewBox="0 0 38 37" fill="none"><path d="M18.1156 1.67634C18.4908 0.965195 19.5092 0.965192 19.8844 1.67634L24.8835 11.1513C25.0282 11.4256 25.2919 11.6172 25.5975 11.6701L36.1535 13.4965C36.9458 13.6336 37.2605 14.6022 36.7001 15.1788L29.2337 22.8612C29.0176 23.0835 28.9168 23.3936 28.961 23.7005L30.4859 34.3043C30.6004 35.1002 29.7764 35.6988 29.0548 35.344L19.4412 30.617C19.163 30.4801 18.837 30.4801 18.5588 30.617L8.94516 35.344C8.22361 35.6988 7.39964 35.1002 7.51409 34.3043L9.03902 23.7005C9.08316 23.3936 8.98242 23.0835 8.76631 22.8612L1.29986 15.1788C0.73946 14.6022 1.05419 13.6336 1.84647 13.4965L12.4025 11.6701C12.7081 11.6172 12.9718 11.4256 13.1165 11.1513L18.1156 1.67634Z" fill="%23F3BC30" stroke="%23F3BC30" stroke-width="2"/></svg>');
  }
  &__info {
    display: inline-block;
    margin-left: 16px;
    color: #000;
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
  }
}

.fill-primary {
  fill: var(--primary) !important;
  stroke: none !important;
}
.fill-white {
  fill: #fff !important;
  stroke: none !important;
}

.field-validation-error {
  margin-top: 2px;
  color: #fb0000;
  font-size: 14px;
}

.input-validation-error {
  border-color: #fb0000;
}

.ui-slider {
  margin-left: -6px;
  margin-right: -6px;
  padding-left: 32px;
  padding-right: 32px;
  .slick-slide {
    padding-left: 6px;
    padding-right: 6px;
  }
  .slick-prev,
  .slick-next {
    width: 24px;
    height: 24px;
    top: calc(50% - 12px);
    z-index: 5;

    &:before {
      opacity: 0.3;
      background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"><path d="M15 19.5L7.5 12L15 4.5" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>');
    }
  }
  .slick-prev {
    left: 6px;
  }
  .slick-next {
    right: 6px;
    transform: rotate(180deg);
  }
}

.cms-page-content {
  img {
    max-width: 100%;
    height: auto;
  }
}

