.b-project {
  padding-top: 40px;
  padding-bottom: 60px;
  &__row {
    display: flex;
    flex-direction: row-reverse;
  }
  &__main {
    width: calc(100% - 448px);
    flex: 0 0 auto;
  }
  &__tags {
    padding-bottom: 24px;
    font-size: 15px;
    font-weight: 300;
    line-height: normal;
    a {
      color: #AA7144;
      &:hover {
        color: #EE7B20;
      }
    }
  }
  &__info {
    padding-bottom: 24px;
    line-height: 25px;
  }
  &__sidebar {
    flex: 0 0 auto;
    width: 400px;
    margin-right: 48px;
  }

  .ui-title-page {
    margin-bottom: 24px;
  }
}

.b-project-section {
  margin: 8px 0;
  &__header {
    border-top: 1px solid #0000002f;
  }
  &__title {
    position: relative;
    width: 100%;
    padding: 28px 0;
    background: transparent;
    border: none;
    text-align: left;
    color: #000;
    font-size: 20px;
    font-weight: 500;
    line-height: 25px;
    &[aria-expanded="false"] {
      .ic {
        transform: rotate(180deg);
      }
    }
    .ic {
      position: absolute;
      right: 0;
      top: auto;
    }
  }
}

.b-project-descrip {
  display: flex;
  padding-bottom: 24px;
  &__title {
    width: 170px;
  }
  &__info {
    display: flex;
    align-items: center;
    color: #000;
    font-weight: 300;
    line-height: 1.25;
    .ic {
      margin-right: 12px;
    }
  }
}

.descrip-last {
  border-bottom: 1px solid #0000002f;
}
.b-project-slider {
  &__img {
    padding-bottom: 10px;
    img {
      aspect-ratio: 1/1;
      width: 100%;
      object-fit: contain;
    }
  }
  &__thumb {
    margin-right: 12px;
    &:first-child {
      padding-left: 0;
    }
    img {
      width: 82px;
      height: 82px;
      object-fit: cover;
      border: 1px solid transparent;
    }
    &.slick-current {
      img {
        border-color: var(--primary);
      }
    }
  }

  .slick-slide {
    img {
      width: 100%
    }
  }
}

.b-project__sidebar {
  position: relative;

  .btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 11px;
    padding-bottom: 11px;
    .ic {
      margin-right: 8px;
    }
  }
}

.b-project-rating {
  position: absolute;
  display: flex;
  align-items: center;
  width: 60px;
  height: 25px;
  top: 8px;
  right: 8px;
  padding-left: 6px;
  padding-right: 8px;
  padding-top: 4px;
  padding-bottom: 4px;
  z-index: 2000;
  color: #000;
  border-radius: 3px;
  background: #FFF;
  &:hover {
    cursor: pointer;
    background: #FDF7ED;
    color: #373737;
    transition: .3s;
  }
  &__ic {
    padding-right: 6px;
    padding-bottom: 3px;
  }
}

.b-project-copyright {
  padding: 36px 0;
  border-bottom: 1px solid #0000002f;
  &__title {
    color: #E06605;
    font-size: 18px;
    font-weight: 500;
    line-height: 28px;
  }
  &__info {
    padding: 8px 0;
    font-size: 14px;
    font-weight: 300;
    line-height: 22px;
  }
  .btn {
    &:hover {
      transition: .3s;
      .ic {
        stroke: #FFF;
      }
    }
    .ic {
      margin-right: 10px;
      stroke: #E06605;
    }
  }
}

.b-project-map {
  padding-top: 46px;
  padding-bottom: 36px;
  border-bottom: 1px solid #0000002f;
  &__title {
    color: #E06605;
    font-size: 18px;
    line-height: 28px;
  }
  &__img {
    padding: 24px 0;
  }
  .btn {
    .ic {
      stroke: #E06605;
    }
  }
}

@include media-breakpoint-down(md) {
  .b-project__row {
    display: block;
  }
  .b-project__sidebar {
    margin-top: 100px;
  }
  .b-project__main {
    width: 100%;
  }
  .b-project__sidebar {
    width: 100%;
    margin-right: 0;
  }
  
}

@include media-breakpoint-down(sm) {
  .b-project-descrip {
    display: block;
  }
  .b-project-map {
    .ic {
      display: none;
    }
  }
  .b-project-copyright {
    .ic {
      display: none;
    }
  }
  .b-project-descrip__title {
    width: 100%;
    margin-bottom: 10px;
  }
}
