.b-filter {
  margin-bottom: 60px;
  padding: 16px;
  border-radius: 13px;
  background: #FDF6EE;
  &__block {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px;
  }
  &__input {
    height: 60px;
    padding-left: 50px;
    padding-right: 156px;
    border-radius: 50px;
    border: 1px solid #EFEAE6;
    flex-grow: 1;
    color: #000;
    font-size: 16px;
    font-weight: 300;
    line-height: 60px;
    background: #fff url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" viewBox="0 0 22 22" fill="none"><g opacity="0.9"><path fill-rule="evenodd" clip-rule="evenodd" d="M1.6991 9.69455C1.6991 5.2788 5.2788 1.6991 9.69455 1.6991C14.1103 1.6991 17.69 5.2788 17.69 9.69455C17.69 11.5993 17.024 13.3484 15.9121 14.7218L19.0035 17.8133C19.3322 18.1419 19.3322 18.6748 19.0035 19.0035C18.6748 19.3322 18.1419 19.3322 17.8133 19.0035L14.7218 15.9121C13.3484 17.024 11.5993 17.69 9.69455 17.69C5.2788 17.69 1.6991 14.1103 1.6991 9.69455ZM9.69455 3.38235C6.20844 3.38235 3.38235 6.20844 3.38235 9.69455C3.38235 13.1807 6.20844 16.0068 9.69455 16.0068C13.1807 16.0068 16.0068 13.1807 16.0068 9.69455C16.0068 6.20844 13.1807 3.38235 9.69455 3.38235Z" fill="black" fill-opacity="0.85"/></g></svg>') no-repeat 16px 50%;
  }
  &__search {
    position: absolute;
    right: 25px;
    top: 25px;
    bottom: 25px;
    width: 140px;
  }
  &__trigger {
    display: inline-flex;
    align-items: center;
    margin-top: 8px;
    margin-bottom: 12px;
    padding: 4px 0;
    color: #E16C10;
    font-size: 15px;
    font-weight: 400;
    line-height: 1.6;
    border: none;
    border-bottom: 1px solid #E16C10;
    background-color: transparent;
    &:after {
      content: '';
      display: inline-block;
      width: 17px;
      height: 16px;
      margin-left: 8px;
      background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="17" height="16" viewBox="0 0 17 16" fill="none"><path d="M3.5 10L8.5 5L13.5 10" stroke="%23E16C10" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></svg>');
    }
    &[aria-expanded="false"] {
      &:after {
        transform: rotate(180deg);
      }
    }
  }
  &__body {
    padding: 24px 24px 12px;
    border-radius: 4px;
    background: #FCECDE;
  }
  &__checkboxes {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 8px;

    .custom-checkbox {
      margin-right: 28px;
      margin-bottom: 12px;
      font-size: 15px;
    }
    .custom-control-label::before {
      background-color: transparent;
    }
  }
  &__selects {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__select {
    width: 258px;
    margin-right: 16px;
    margin-bottom: 16px;
  }


  @include media-breakpoint-down(sm) {
    margin-bottom: 20px;
    &__block {
      padding-left: 0;
      padding-right: 0;
    }
    &__input {
      width: 100%;
      padding-right: 100px;
    }
    &__search {
      right: 1px;
      width: auto;
    }
  }
}
