.header {
  // position: absolute;
  // top: 0;
  // left: 0;
  // right: 0;
  // z-index: 10;

  &__logo {
    display: inline-flex;
    align-items: center;
    max-width: 455px;
    color: #2C2620;
    font-family: 'e-Ukraine Head';
    font-weight: 500;
    font-size: 20px;
    line-height: normal;

    img {
      width: 165px;
      margin-right: 20px;
    }
  }

  @include media-breakpoint-down(lg) {
    &__logo {
      font-size: 18px;
      img {
        max-width: 150px;
      }
    }
  }

  @include media-breakpoint-down(md) {
    &__logo {
      font-size: 16px;
    }
  }

  @include media-breakpoint-down(sm) {
    &__logo {
      font-size: 0;
      img {
        max-width: 100px;
        margin-right: 0;
      }
    }
  }
}

.header-top {
  padding-top: 21px;
  padding-bottom: 21px;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  &__group {
    display: flex;
    align-items: center;
  }
  &__user {
    display: flex;
    align-items: center;
    margin-left: 28px;
    padding: 11px 16px 11px 12px;
    border-radius: 9px;

    .ic {
      margin-right: 8px;
      fill: #E06605;
      transition: all .3s;
    }

    &:hover {
      .ic {
        fill: #fff;
      }
    }
  }


  @include media-breakpoint-down(md) {
    padding-top: 8px;
    padding-bottom: 8px;
    &__inner {
      padding-right: 30px;
    }
    &__user {
      font-size: 0;
      .ic {
        margin-right: 0;
      }
    }
  }


  @include media-breakpoint-down(sm) {
    &__inner {
      padding-right: 50px;
    }
    &__user {
      margin-bottom: 12px;
      margin-left: 0;
      font-size: 14px;
      .ic {
        margin-right: 8px;
      }
      &_desc {
        display: none;
      }
    }
  }
}

.header-nav {
  padding: 0;
  background: #FDF7ED;

  @include media-breakpoint-down(md) {
    position: relative;
    z-index: 1000;
    &__inner {
      position: fixed;
      top: 74px;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 1000;
      overflow-y: auto;
    }
  }
}

.header-soc {
  &__item {
    margin-left: 9px;
    fill: #2C2620;
    opacity: 0.5;
    &:hover {
      opacity: 0.8;
    }
  }

  @include media-breakpoint-down(md) {
    margin-top: 10px;

    &_desc {
      display: none;
    }
  }
}

.header-lanquage {
  margin-right: 14px;
  .dropdown-toggle {
    font-size: 13px;
    font-weight: 300;
    color: #2C2620;
  }
  .dropdown-menu {
    min-width: 70px;
  }
}

.header-search {
  display: flex;
  align-items: center;
  margin-right: 28px;
  margin-top: 2px;
  &__btn {
    margin-right: 7px;
    padding: 0;
    border: none;
    background-color: transparent;
    stroke: #2C2620;
    .ic {
      stroke: inherit;
    }

    &:hover {
      stroke: var(--primary);
    }
  }
  &__input {
    width: 50px;
    padding: 0;
    border: none;
    color: #2C2620;
    font-size: 13px;
    font-weight: 300;
    line-height: 1;
    transition: all .3s;
    border-radius: 2px;
    &:focus {
      width: 150px;
      padding-left: 6px;
      outline: none;
      box-shadow: 0 0 5px #ccc;
    }

    &::placeholder {
      color: #2C2620;
    }
  }

  @include media-breakpoint-down(lg) {
    &__input {
      &:focus {
        width: 50px;
        padding-left: 0;
      }
    }
  }
}

.toggle-menu-button {
  position: absolute;
  right: 0;
  top: 23px;
  bottom: 0;
  width: 58px;
  height: 58px;
  z-index: 200;
  background-color: var(--primary);
  transition: all .3s;
  border-radius: 0;
  .toggle-menu-button-icon {
    position: relative;
    display: inline-block;
    width: 22px;
    height: 17px;
    vertical-align: middle;
    cursor: pointer;
    transition: 0.5s ease-in-out;
    span {
      position: absolute;
      display: block;
      width: 50%;
      height: 3px;
      opacity: 1;
      transition: 0.25s ease-in-out;
      transform: rotate(0deg);
      background-color: #FFF;
      &:nth-child(even) {
        left: 50%;
        border-radius: 0 9px 9px 0;
      }
      &:nth-child(odd) {
        left: 0;
        border-radius: 9px 0 0 9px;
      }
      &:nth-child(1),
      &:nth-child(2) {
        top: 0;
      }
      &:nth-child(3),
      &:nth-child(4) {
        top: 8px;
      }
      &:nth-child(5),
      &:nth-child(6) {
        top: 16px;
      }
    }
  }

  &.show {
    .toggle-menu-button-icon {
      span {
        &:nth-child(1),
        &:nth-child(6) {
          transform: rotate(45deg);
        }
        &:nth-child(2),
        &:nth-child(5) {
          transform: rotate(-45deg);
        }
        &:nth-child(1) {
          top: 4px;
          left: 2px;
        }
        &:nth-child(2) {
          top: 4px;
          left: calc(50% - 2px);
        }
        &:nth-child(3) {
          left: -50%;
          opacity: 0;
        }
        &:nth-child(4) {
          left: 100%;
          opacity: 0;
        }
        &:nth-child(5) {
          top: 11px;
          left: 2px;
        }
        &:nth-child(6) {
          top: 11px;
          left: calc(50% - 2px);
        }
      }
    }
  }

  @include media-breakpoint-down(sm) {
    top: 10px;
  }
}
